            <div class="d-flex justify-content-between align-items-center">
                <div class="Penaltitle gray-text">
                    來電分析概覽
                </div>
            </div>
            <div class="row">
                <div class="col-md-8">
                    <div class="mt-3">
                        <div class="row">
                            <div class="col-md-12 col-lg-5 d-flex justify-content-center align-items-center">
                                <apexchart type="donut" width="250" :options="ChartDonutOption.options" :series="ChartDonutOption.series"></apexchart>
                            </div>
                            <div class="d-flex flex-md-column col-lg-6 justify-content-center CircleWrap">
                                <div class="Circle blueCircle">
                                    <span class="pr-2 gray-text white-nowrap pb-2 titleWidth">自己接聽</span>
                                    <span class="d-flex align-items-end  justify-content-end min-width-100">
                                        <span class="xl-text font-weight-bold CountWidth text-right">{{AnalysisData.ByMyself}}</span>
                                        <span class="gray-text pb-2 pl-1"> 通</span>
                                    </span>
                                    <span class="md-text white-text pb-2 font-weight-bold white-nowrap pl-3">{{(AnalysisData.ByMyself / this.AnalysisData.percent * 100).toFixed(1)}} %</span>
                                </div>
                                <div class="Circle orangeCircle ">
                                    <span class="pr-2 gray-text white-nowrap pb-2 titleWidth">總部代接</span>
                                    <span class="d-flex align-items-end  justify-content-end min-width-100">
                                        <span class="xl-text font-weight-bold CountWidth text-right">{{AnalysisData.CenterPick}}</span>
                                        <span class="gray-text pb-2 pl-1"> 通</span>
                                    </span>
                                     <span class="md-text white-text pb-2 font-weight-bold white-nowrap pl-3">{{(AnalysisData.CenterPick / this.AnalysisData.percent * 100).toFixed(1)}} %</span>
                                </div>
                                <div class="Circle pinkCircle">
                                    <span class="pr-2 gray-text white-nowrap pb-2 titleWidth">全程漏接</span> 
                                    <span class="d-flex align-items-end  justify-content-end min-width-100">
                                        <span class="xl-text font-weight-bold CountWidth text-right">{{AnalysisData.Miss}}</span>
                                        <span class="gray-text pb-2 pl-1"> 通</span>
                                    </span>
                                    <span class="md-text white-text pb-2 font-weight-bold white-nowrap pl-3">{{(AnalysisData.Miss / this.AnalysisData.percent * 100).toFixed(1)}} %</span>
                                </div>
                                <div class="Circle yellowCircle">
                                    <span class="pr-2 gray-text white-nowrap pb-2 titleWidth">下班來電</span> 
                                    <span class="d-flex align-items-end  justify-content-end min-width-100">
                                        <span class="xl-text font-weight-bold CountWidth text-right">{{AnalysisData.AfterWork}}</span>
                                        <span class="gray-text pb-2 pl-1"> 通</span>
                                    </span>
                                    <span class="md-text white-text pb-2 font-weight-bold white-nowrap pl-3">{{(AnalysisData.AfterWork / this.AnalysisData.percent * 100).toFixed(1)}} %</span>
                                </div>
                                <div class="Circle grayCircle">
                                    <span class="pr-2 gray-text white-nowrap pb-2 titleWidth">未按IVR</span> 
                                    <span class="d-flex align-items-end  justify-content-end min-width-100">
                                         <span class="xl-text font-weight-bold CountWidth text-right">{{AnalysisData.NoAnswer}}</span>
                                        <span class="gray-text pb-2 pl-1"> 通</span>
                                    </span>
                                    <span class="md-text white-text pb-2 font-weight-bold white-nowrap pl-3">{{(AnalysisData.NoAnswer / this.AnalysisData.percent * 100).toFixed(1)}} %</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <hr />
                    <div class="d-flex ChartBox align-items-center">
                        <div class="gray-text mr-3 col-md-4 text-center">
                            <span class="white-text xl-text font-weight-bold pr-1">30</span>通<br>
                            已接回報
                        </div>
                        <div class="col-md-8">
                            <apexchart ref="ReportCate" width="100%" type="bar" :options="ReportCateOption.options" :series="ReportCateOption.series"></apexchart>
                        </div>
                    </div>
                </div>
                <div class="col-md-4">
                    <div class="OtherDataBox h-100 d-flex flex-md-column justify-content-around align-items-center">
                        <div class="gray-text text-center">
                            <span class="white-text xl-text font-weight-bold pr-1">21</span>通<br>
                            獨立門號
                        </div>

                        <div class="gray-text text-center" >
                            <span class="white-text xl-text font-weight-bold pr-1">3</span>通<br>
                            立即預約
                        </div>
                        <div class="gray-text text-center">
                            <span class="white-text xl-text font-weight-bold pr-1">5</span>通<br>
                            總預約
                        </div>
                    </div>
                </div>
            </div>
    </div>
</template>
<script>
    import Vue from 'vue';
    import { mapState } from 'vuex'
    import 'vue-css-donut-chart/dist/vcdonut.css';

    import VueApexCharts from 'vue-apexcharts'
    Vue.use(VueApexCharts);



    export default {
        data() {
            return {
                ReportCateOption: {
                    options: {
                        plotOptions: {
                            bar: {
                                columnWidth: '30%',
                                distributed: true,
                                dataLabels: {
                                    position: 'top',
                                },
                            }
                        },
                        chart: {
                            id: 'vuechart-reportCate',
                            toolbar: {
                                show: false,
                            }
                        },
                        xaxis: {
                            categories: ['商機', '服務', '客訴', '其他'],
                            labels: {
                                style: {
                                    colors: ['#93959C', '#93959C', '#93959C', '#93959C'],
                                    fontSize: '12px'
                                }
                            },
                            axisTicks: {
                                show: false,
                            },
                            axisBorder: {
                                show: false,
                            }
                        },
                        yaxis: {
                            show: false,
                        },
                        colors: ['#FF8856', '#0090FF', '#EF4D45', '#FDC86A'],
                        grid: {
                            borderColor: '#33363D'
                        },
                        legend: {
                            show: false,
                        },
                        tooltip: {
                            enabled: false,
                        },
                        dataLabels: {
                            distributed: true,
                            offsetY: -20,
                            style: {
                                fontSize: '14px',
                            }
                        }
                    },
                    series: [{
                        name: 'series-1',
                        data: [0, 0, 0, 0]
                    }]
                },
                ChartDonutOption: {
                    series: [12,26,3,33,18],
                    options: {
                        dataLabels: {
                            enabled: false,
                        },
                        labels: ["自己接聽", "總部代接", "全部人漏接", "下班來電","未按IVR"],
                        colors: ['#0090FF', '#FF8856', '#EF4D45', '#FDC86A','#B5B7BF'],
                        legend: {
                            show: false
                        },
                        chart: {
                            type: 'donut',
                        },
                        stroke: {
                            width: 0
                        },
                        tooltip: {
                            fixed: {
                                enabled: false
                            },
                        },
                        plotOptions: {
                            pie: {
                                donut: {
                                    size: '80%',
                                    background: 'transparent',
                                    labels: {
                                        show: true,
                                        name: {
                                            show: true,
                                            color: '#93959C',
                                            offsetY: 25
                                        },
                                        value: {
                                            show: true,
                                            fontSize: '16px',
                                            color: '#FCFCFC',
                                            fontSize: '36px',
                                            offsetY: -20,
                                            formatter: function (val) {
                                                return val
                                            }
                                        },
                                        total: {
                                            show: true,
                                            label: '總來電數',
                                            color: '#93959C',
                                            formatter: function (w) {
                                                return w.globals.seriesTotals.reduce((a, b) => {
                                                    return a + b
                                                }, 0)
                                            }
                                        }
                                    }

                                },
                            }
                        }
                    },
                },
                categoryData: [],
                tabList: [
                    { id: 1, itemName: '本日', value: 'today' },
                    { id: 2, itemName: '本月', value: 'month' },
                    { id: 3, itemName: '本年', value: 'year' }
                ],
                sections: [
                    { label: 'business section', value: 0, color: '#FF8856' },
                    { label: 'service section', value: 0, color: '#FDC86A' },
                    { label: 'other section', value: 0, color: '#0090FF' },
                    { label: 'complain section', value: 0, color: '#EF4D45' }
                ],
                EIPReservationData: {
                    RightNow: 0,
                    All:0,
                },
                AnalysisData: {
                    ByMyself:'',
                    CenterPick:'',
                    AfterWork :'',
                    Miss:'',
                    NoAnswer: '',
                    OnlyPhone: '',
                    percent:0,
                }
            }
        },
        computed: {
            ...mapState(['CTI']),
        },
        mounted() {
                this.ChartDonutOption.series = [ 7, 3, 5, 6, 8]
                this.AnalysisData.ByMyself = 7
                this.AnalysisData.CenterPick = 3
                this.AnalysisData.AfterWork = 6
                this.AnalysisData.Miss = 5
                this.AnalysisData.NoAnswer = 8
                this.AnalysisData.OnlyPhone = 3
                this.EIPReservationData.RightNow = 5
                this.EIPReservationData.All = 3
                let percent = this.AnalysisData.ByMyself + this.AnalysisData.NoAnswer + this.AnalysisData.Miss + this.AnalysisData.AfterWork + this.AnalysisData.CenterPick
                this.AnalysisData.percent = percent
                this.update()
        },
        methods: {
            update(){
                this.ReportCateOption.series = [{
                    data: [5, 8, 4, 12]
                }]
            }
        },
        components: {
            apexchart: VueApexCharts,
        }
    }
</script>
<style>
    .ChartBox {
        justify-content: center;
    }

    .OtherDataBox {
        background: #1D1E24;
        border-radius: 20px;
        padding:15px;
    }

    .mobile-style {
        width: 130px;
        height: 160px;
        flex-wrap: wrap;
        align-items: center;
    }



    .CountBox {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 57px;
        align-items: flex-end;
        padding-bottom: 3px;
    }
    /*----------------CHART---------------*/

    .cdc-container {
        margin: 30px 30px 30px 0;
    }

    #chart-donuts-info .center {
        background: #3D3D4A;
        border-radius: 50%;
        height: 60%;
        left: 20%;
        position: absolute;
        top: 20%;
        width: 60%;
    }

    .center div {
        color: #7a7a7a;
        display: flex;
        flex-direction: column;
        font-size: 36px;
        background: #23262b;
        border-radius: 50%;
        position: absolute;
        width: 85%;
        height: 85%;
        align-items: center;
        justify-content: center;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%,-50%);
        transform: translate(-50%,-50%);
    }

    .center .count {
        display: block;
        color: #FCFCFC;
        font-weight: normal;
    }

    .center .text {
        display: block;
        color: #93959C;
    }

    .custom-chart-wrap {
        position: relative;
        width: 170px;
        height: 170px;
        margin: 35px auto;
        border-radius: 100%;
    }

    .item {
        border-radius: 50%;
        clip: rect(0px, 85px, 200px, 0px);
        height: 100%;
        position: absolute;
        width: 100%;
        font-family: monospace;
        font-size: 1.5rem;
    }

    .chart-donuts {
        border-radius: 50%;
        clip: rect(0px, 200px, 200px, 85px);
        height: 100%;
        position: absolute;
        width: 100%;
        transition: .3s;
    }

    #chart-donuts-blue .item {
        background-color: #0090FF;
        transform: rotate(0deg);
    }

    #chart-donuts-yellow .item {
        background-color: #FDC86A;
        transform: rotate(0deg);
    }

    #chart-donuts-pink .item {
        background-color: #EF4D45;
        transform: rotate(0deg);
    }

    #chart-donuts-orange .item {
        background-color: #FF8856;
        transform: rotate(0deg);
    }

    #chart-donuts-all .item {
        background-color: gray;
        transform: rotate(0deg);
    }

    .Circle {
        display: flex;
        align-items: flex-end;
        position: relative;
        padding: 0 10px 0px 20px;
    }

        .Circle:before {
            position: absolute;
            content: '';
            width: 10px;
            height: 10px;
            bottom: 13px;
            border-radius: 50%;
            left: 0px;
            transform: translateX(-50%);
        }
    .DataWidth {
        min-width: 80px;
    }

    .CountWidth {
        min-width: 45px;
    }

    .titleWidth {
        min-width: 70px;
    }

    .min-width-100{
        min-width:100px
    }

    .blueCircle:before {
        background: #0090FF;
    }

    .orangeCircle:before {
        background: #FF8856;
    }

    .pinkCircle:before {
        background: #EF4D45;
    }

    .yellowCircle:before {
        background: #FDC86A;
    }

    .grayCircle:before {
        background: #B5B7BF;
    }

    .TodayPhoneCate hr {
        border-top: 1px solid #484B54;
        margin: 35px 0 30px 0;
        position: relative;
    }

    /*  .TodayPhoneCate hr:after {
            content: "";
            width: 20px;
            height: 20px;
            border-top: 1px solid #484B54;
            border-left: 1px solid #484B54;
            position: absolute;
            left: 40%;
            transform: rotate(45deg);
            background: #23262b;
            top: -10px;
        }*/

    @media screen and (max-width:1375px) {
        .flex-column.mobile-style {
            flex-direction: unset !important;
        }

        

        .mobile-style {
            width: 50%;
            justify-content: center;
        }

        .ChartBox {
            flex-wrap: wrap;
        }

        .cdc-container {
            width: 50%;
            margin-right: 0;
        }
    }

    @media screen and (max-width:992px) {
        .CircleWrap{
            flex-wrap:wrap;
        }
    }
    @media screen and (max-width: 689px) {
        .Circle {
            width: 51%;
        }
        
    }
</style>