<template>
  <div id="DashBoard">
    <DataSidebar
      :SideBarTab="CTI.SideBarTab"
      v-on:ChangeSideBarTab="ChangeSideBarTab"
      v-on:getCallInData="getCallInData"
      v-on:GetAudioRecord="GetAudioRecord"
    ></DataSidebar>

    <div class="d-flex justify-content-between Penal-bottom flex-wrap">
      <div class="PageTitle">{{ ServicePhone }} - 總覽分析</div>
      <datetime
        v-model="datePicker.StartValue"
        class="theme-dark datePicker ml-2 position-absolute"
        style="border: 0; width: 0px"
        :max-datetime="todayformat"
        required
        auto
        :phrases="{ ok: '確認', cancel: '取消' }"
      ></datetime>
      <datetime
        v-model="datePicker.EndValue"
        class="theme-dark datePicker ml-2 position-absolute"
        style="border: 0; width: 0px"
        :min-datetime="datePicker.StartValue"
        :max-datetime="todayformat"
        required
        auto
        :phrases="{ ok: '確認', cancel: '取消' }"
      ></datetime>
      <div class="d-flex flex-wrap">
        <div
          v-for="(item, index) in tabList.list"
          :key="index"
          class="tab-item"
          :class="{ active: index == tabList.tabNum }"
          @click="getCallInCategroy(index, item.value, item.isRange)"
        >
          {{ item.itemName }}
        </div>
        <div
          class="datePicker d-flex align-items-center"
          @click="openStartData"
          v-if="
            datePicker.FinalStartValue !== '' && datePicker.FinalEndValue !== ''
          "
        >
          {{ datePicker.FinalStartValue }} 至 {{ datePicker.FinalEndValue }}
        </div>
        <div
          v-else
          class="datePicker d-flex align-items-center"
          @click="openStartData"
        >
          請選擇日期
        </div>
      </div>
    </div>
    <div class="Penal-bottom">
      <div class="row BusinessBox">
        <b-col xl="4" lg="4" md="4" class="BusinessCol">
          <DataCategroy
            :status="statusForCategory"
            :CallinNoAnswerList="CallinNoAnswerHistoryTable"
            :NeedCallBackList="NeedCallBackList"
            :NeedReportList="itemsReportTable"
            :GetNeedTransToEIPListTable="GetNeedTransToEIPListTable"
            :CallinRestTimeTable="CallinRestTimeTable"
          ></DataCategroy>
        </b-col>
        <b-col xl="8" lg="8" md="8" class="BusinessChartCol">
          <ChartDonuts></ChartDonuts>
        </b-col>
      </div>
    </div>
    <div class="row">
      <b-col xl="6" lg="6" md="6" sm="12" class="Penal-bottom">
        <IvrAnalysis :status="statusForPhoneTime"></IvrAnalysis>
      </b-col>
      <b-col xl="6" lg="6" md="6" sm="12">
        <PhoneTimeAvg ></PhoneTimeAvg>
      </b-col>
    </div>
    <b-col xl="12" lg="12" md="12" class="pl-0 pr-0 Penal-bottom">
      <div class="style-background-gray Panel-Style">
        <DataTable
          ref="CallInHistoryTable"
          tableName="CallInHistoryTable"
          :itemsTable="CallInHistory.item"
          :fieldsTable="fieldsHistoryTable"
          :busy="CallInHistory.status"
          perPage="10"
          sortBy="dialDT"
          sortDirection="desc"
          :filterIncludedFields="CallInReportSetting.filterOn"
          v-on:getCallInData="getCallInData"
          v-on:ChangeSideBarTab="ChangeSideBarTab"
          v-on:GetCustomerData="GetCustomerData"
          :MyAgentNo="MyAgentNo"
          :MyAgentAllData="MyAgentAllData"
          v-if="CallInHistory.item"
        >
          <template v-slot:title> 通話紀錄 - {{ ServicePhone }} </template>
        </DataTable>
      </div>
    </b-col>
  </div>
</template>
<script>
import DataTable from "../components/DataTable";
import ChartDonuts from "../components/ChartDonuts";
import ChartLine from "../components/ChartLine";
import PhoneTimeAvg from "../components/PhoneTimeAvg";
import DataSidebar from "../components/DataSidebar";
import DataCategroy from "../components/DataCategroy";
import IvrAnalysis from "../components/IvrAnalysis";
import { Datetime } from "vue-datetime";
import "vue-datetime/dist/vue-datetime.css";
import { mapState } from "vuex";

import ServiceList from "../JsonData/ServiceList"
import MyExtList from "../JsonData/MyExtList"
import CallinHistory from "../JsonData/CallinHistory"
import CallInCategory from "../JsonData/CallInCategory"
import CallinAllAnalysis from "../JsonData/CallinAllAnalysis"
import CallInPhoneTime from "../JsonData/CallInPhoneTime"
import EIPToReservation from "../JsonData/EIPToReservation"
import {data1,data2} from "../JsonData/CustomerData"
import CallInLineATData from "../JsonData/CallInLineAT"

import CallinNoAnswerData from "../JsonData/CallinNoAnswerData"
import CallinReportList from "../JsonData/CallinReportList"
import NeedCallBackList from "../JsonData/NeedCallBackList"
import TransToEIPReservation from "../JsonData/TransToEIPReservation"
import CallinOnWestTime from "../JsonData/CallinOnWestTime"

let timeoutID;


export default {
  props: ["ServicePhone"],
  data() {
    return {
      //撈資料狀態
      statusForCategory: true,
      statusForPhoneTime: true,
      statusForChartLine: true,
      //日期選擇器設定
      datePicker: {
        maxdate: "",
        StartValue: "",
        EndValue: "",
        FinalStartValue: "",
        FinalEndValue: "",
      },
      //TabList 日期區間
      tabList: {
        list: [
          { id: 1, itemName: "近七天", value: "week", isRange: true },
          { id: 2, itemName: "本日", value: "today", isRange: true },
          { id: 3, itemName: "本月", value: "thisMonth", isRange: false },
          { id: 4, itemName: "本年", value: "thisYear", isRange: false },
        ],
        tabNum: 0,
      },
      //電話進來CallINDATA
      AllInfoToPopUp: "",
      //我的0800 AgentNo列表
      MyAgentNo: [],
      MyAgentAllData: "",
      //我的登入資訊
      UserInfo: "",
      //SignalR 訊息 測試用
      message: "",
      //這通電話回報的內容
      ReportData: "",
      //通話紀錄LISTtable
      CallInHistory: {
        item: [],
        status: "",
      },
      //未接來電列表
      CallinNoAnswerHistoryTable: {
        dayDiff: "",
        item: [],
        status: "",
      },
      //需回電列表
      NeedCallBackList: {
        dayDiff: "",
        item: [],
        status: "",
      },
      //轉預約資料
      EIPToReservationList: {
        item: [],
        status: "",
      },
      //取得五大通話分析資料
      CallinAllAnalysisList: {
        item: [],
        status: "",
      },
      //需回報列表
      itemsReportTable: {
        dayDiff: "",
        item: [],
        status: "",
      },
      fieldsHistoryTable: [
        {
          key: "callinID",
          label: "編號",
          class: "table-font-white mobile-none",
          sortable: true,
        },
        {
          key: "name",
          label: "稱呼",
          class: "table-font-white nameColumn",
          sortable: true,
        },
        {
          key: "fromPhoneNo",
          label: "電話",
          class: "table-font-white mobile-none",
          sortable: true,
        },
        {
          key: "dialDT",
          label: "月/日 來電時間",
          class: "mobile-none",
          sortDirection: "desc",
          sortable: true,
        },
        {
          key: "upAgent",
          label: "接聽分機",
          class: "table-font-white mobile-none",
          sortable: true,
        },
        { key: "soundlen", label: "錄音帶", sortable: false },
        { key: "more", label: "更多", class: "mobile-show", sortable: false },
      ],
      GetNeedTransToEIPListTable: {
        dayDiff: "",
        item: [],
        status: "",
      },
      CallinRestTimeTable: {
        dayDiff: "",
        item: [],
        status: "",
      },
      //Datatable 過濾設定 共用
      CallOutReportSetting: {
        filterOn: ["callOutPhoneNo"],
      },
      CallInReportSetting: {
        filterOn: ["name", "fromPhoneNo"],
      },
      selectTimeNum: 0,
    };
  },

  watch: {
    //第一次選日期會少一天 故利用selectTimeNum 判斷第一次選的話 + 1 天  //解決BUG用 待未來改寫
    "datePicker.StartValue": function (newValue, oldValue) {
      if (this.selectTimeNum == 0) {
        let FormatDate = new Date(newValue).toLocaleDateString();
        let FormateDateToString = new Date(
          new Date(FormatDate).setDate(new Date(FormatDate).getDate() + 1)
        ).toLocaleDateString();
        this.datePicker.FinalStartValue = FormateDateToString.slice(0, 10);
      } else {
        this.datePicker.FinalStartValue = newValue.slice(0, 10);
      }

      setTimeout(() => {
        document.getElementsByClassName("vdatetime-input")[1].click();
      }, 300);
    },
    "datePicker.EndValue": function (newValue, oldValue) {
      if (this.selectTimeNum == 0) {
        let FormatDate = new Date(newValue).toLocaleDateString();
        let FormateDateToString = new Date(
          new Date(FormatDate).setDate(new Date(FormatDate).getDate() + 1)
        ).toLocaleDateString();
        this.datePicker.FinalEndValue = FormateDateToString.slice(0, 10);
      } else {
        this.datePicker.FinalEndValue = newValue.slice(0, 10);
      }
      this.selectTimeNum += 1;
      this.getDateTimeForCallInCategory("RangeDate", false);
    },
  },
  mounted() {
    //得到該0800分機電話
    let _this = this;
    //取得登入者的資訊
    let storage = JSON.parse(localStorage.getItem("UserInfo"));
    this.UserInfo = storage;
    //取得我負責的分機列表
    this.GetMyExt(this.UserInfo.accountID);
    //取得我負責的分機
    this.getServicePhoneToAgentList();
    //取得所有通話資料
    setTimeout(() => {
      _this.getDateTimeForCallInCategory("week", true);
    }, 400);
  },
  methods: {
    openStartData() {
      document.getElementsByClassName("vdatetime-input")[0].click();
    },
    ChangeSideBarTab(tab) {
      this.$store.dispatch("handChangeSideBarTab", tab);
    },
    //取得分機
    getServicePhoneToAgentList() {
      this.$store.dispatch("handchangeExt", ServiceList);
    },
    //取得通話歷史紀錄
    getCallInHistory(StartDate, EndDate) {
      let myServicePhone = this.$route.params.ServicePhone;
      this.CallInHistory.item = [];
      this.CallInHistory.status = 200;
      this.CallInHistory.item = CallinHistory;
      this.$refs.CallInHistoryTable.resetFilter();
    },
    //取得未接通話紀錄
    getNoAnswerHistory(StartDate, EndDate) {
        let myServicePhone = this.$route.params.ServicePhone
        this.CallinNoAnswerHistoryTable.item = []
        this.CallinNoAnswerHistoryTable.item = CallinNoAnswerData
    },
    //取得須回報通話列表
    getReportList(StartDate, EndDate) {
        let myServicePhone = this.$route.params.ServicePhone
        this.itemsReportTable.item = []
        this.itemsReportTable.item = CallinReportList
    },
    //取得須回電列表
    GetNeedCallBackList(StartDate, EndDate) {
        let myServicePhone = this.$route.params.ServicePhone
        this.NeedCallBackList.item = []
        this.NeedCallBackList.item = NeedCallBackList

    },
    //取得須轉EIP列表
    GetNeedTransToEIPList(StartDate, EndDate) {
        let myServicePhone = this.$route.params.ServicePhone
        this.GetNeedTransToEIPListTable.item = []
        this.GetNeedTransToEIPListTable.item = TransToEIPReservation
    },
      GetCallinRestTimeWorkList(StartDate, EndDate) {
        this.CallinRestTimeTable.item = CallinOnWestTime
    },
    //取得客戶資料 (用手機)
    GetCustomerData(Cellphone) {
      this.$store.dispatch("handGetCustomerData", "");
      if(Cellphone == '0963356659'){
        this.$store.dispatch("handGetCustomerData", data1);
        this.$store.dispatch("handGetCustomerLineAtData", CallInLineATData);
      } else if(Cellphone == '0934256284'){
        this.$store.dispatch("handGetCustomerData", data2);
        this.$store.dispatch("handGetCustomerLineAtData", []);
      }
    },
    //取得這通電話的相關資料
    getCallInData(CallInID) {
        this.$store.dispatch('handGetCallInID', CallInID)
        this.$store.dispatch('handGetCallInData', CallinData)
    },
    GetMyExt() {
        this.MyAgentAllData = MyExtList;
        for (let i in MyExtList) {
            this.MyAgentNo.push(MyExtList[i]["agentNo"]);
        }
    },
    initialStatus(Agent) {
      fetch(initialStatusUrl + Agent, {
        method: "POST",
      })
        .then((response) => {
          return response.json();
        })
        .then((jsonData) => {});
    },
    imgError(item) {
      item.pictureUrl = require("../assets/DefaultPic.png");
    },
    
    //取得錄音黨
    GetAudioRecord(CallinID) {
      this.$store.dispatch("handChangeAudioRecord", "");
      fetch(GetAudioRecordUrl + CallinID, {
        method: "POST",
      })
        .then((response) => {
          return response.json();
        })
        .then((jsonData) => {
          this.$store.dispatch("handChangeAudioRecord", jsonData);
        })
        .catch((err) => {
          console.log("錯誤:", err);
        });
    },
    getCallInCategroy(index, value, isRange) {
      this.tabList.tabNum = index;
      this.getDateTimeForCallInCategory(value, isRange);
    },
    //處理時間並執行getCallInCategory
    getDateTimeForCallInCategory(tab, isRange) {
      let StartDate;
      let EndDate;
      if (isRange) {
        EndDate = new Date(
          new Date().setDate(new Date().getDate() + 1)
        ).toLocaleDateString();
        if (tab == "week") {
          StartDate = new Date(
            new Date().setDate(new Date().getDate() - 7)
          ).toLocaleDateString();
        } else if (tab == "month") {
          StartDate = new Date(
            new Date().setDate(new Date().getDate() - 30)
          ).toLocaleDateString();
        } else if (tab == "year") {
          StartDate = new Date(
            new Date().setDate(new Date().getDate() - 365)
          ).toLocaleDateString();
        } else if (tab == "today") {
          StartDate = new Date().toLocaleDateString();
        }
      } else {
        if (tab == "thisMonth") {
          StartDate =
            new Date(new Date().setMonth(new Date().getMonth()))
              .toISOString()
              .substring(0, 8) + "01";
          EndDate =
            new Date(new Date().setMonth(new Date().getMonth() + 1))
              .toISOString()
              .substring(0, 8) + "01";
        } else if (tab == "thisYear") {
          StartDate =
            new Date(new Date().setFullYear(new Date().getFullYear()))
              .toISOString()
              .slice(0, 4) + "/01/01";
          EndDate =
            new Date(new Date().setFullYear(new Date().getFullYear() + 1))
              .toISOString()
              .slice(0, 4) + "/01/01";
        } else if (tab == "RangeDate") {
          StartDate = this.datePicker.FinalStartValue;
          EndDate = this.datePicker.FinalEndValue;
        }
      }
      this.getServicePhoneToAgentList();
      //取得通話分類數據
      this.GetCallInCategoryOneDay(StartDate, EndDate);
      //取得平均通話時間數據
      this.GetCallinPhoneTimeOneDay(StartDate, EndDate);
      //取得通話紀錄列表
      this.getCallInHistory(StartDate, EndDate);
      //取得轉預約資料
      this.GetEIPToReservation(1, StartDate, EndDate);
      //取得五大通話分析資料
      this.GetCallinAllAnalysis(StartDate, EndDate);

      //取得需回報記錄列表
      this.getReportList(StartDate, EndDate)
      //取得未接通話列表
      this.getNoAnswerHistory(StartDate, EndDate)
      //取得需回電列表
      this.GetNeedCallBackList(StartDate, EndDate)
      //取得下班來電列表
      this.GetCallinRestTimeWorkList(StartDate, EndDate)
      //取得轉預約列表
      this.GetNeedTransToEIPList(StartDate, EndDate)
      //解決BUG用
      this.$refs.CallInHistoryTable.resetFilter();
    },
    //取得商機 客訴 服務 總來電通數 漏接通數 (區間)
    GetCallInCategoryOneDay(StartDate, EndDate) {
      this.$store.dispatch("handGetCallInCategory", CallInCategory);
    },
    //取得平均通話時間 指定日
    GetCallinPhoneTimeOneDay(StartDate, EndDate) {
      this.statusForPhoneTime = true;
      this.$store.dispatch("handGetCallInPhoneTime", CallInPhoneTime);
      this.statusForPhoneTime = false;
    },
    GetEIPToReservation(toResvType, StartDate, EndDate) {
      this.EIPToReservationList.item = [];
      this.EIPToReservationList.item = EIPToReservation;
    },
    GetCallinAllAnalysis(StartDate, EndDate) {
      this.statusForCategory = true;
      this.CallinAllAnalysisList.item = [];
      this.CallinAllAnalysisList.item = CallinAllAnalysis
      this.statusForCategory = false;
    },
  },
  components: {
    ChartDonuts,
    PhoneTimeAvg,
    DataTable,
    DataSidebar,
    //ChartLine,
    datetime: Datetime,
    DataCategroy,
    IvrAnalysis,
  },
  computed: {
    ...mapState(["CTI"]),

    //右上角日歷 預設只能選到今天
    todayformat: function () {
      var rightNow = new Date();
      var res = rightNow.toISOString().slice(0, 10);

      return res;
    },
  },
};
</script>
<style>
#DashBoard .TodayPhoneCate {
  height: 100%;
}

/*Datable樣式*/
#DashBoard .b-table-empty-row {
  color: #fcfcfc;
}

#DashBoard .PageTitle {
  font-size: 24px;
}

.reportBtn {
  width: 80px;
  height: 30px;
  background: #0090ff1c;
  border-radius: 10px;
  color: #0090ff;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;
}

.reportBtn.isHover {
  background: #0090ff;
  color: #fcfcfc;
}

.table-font-white {
  color: #fcfcfc;
}

.table-font-gray {
  color: #93959c;
}

#DashBoard .page-link {
  background: transparent;
  border: 0px;
  color: white;
  width: 34px;
  height: 34px;
}

#DashBoard .page-item.active .page-link {
  color: #0090ff;
}

#DashBoard .page-item:first-child .page-link {
  background: #1d1e24;
  border: 0;
  display: none;
}

#DashBoard .page-item:nth-child(2) .page-link {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1d1e24;
  border: 0;
  color: #0090ff;
  border-radius: 10px;
}

#DashBoard .page-item:last-child .page-link {
  background: #1d1e24;
  border: 0;
  display: none;
}

#DashBoard .page-item:nth-last-child(2) .page-link {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1d1e24;
  border: 0;
  color: #0090ff;
  border-radius: 10px;
}

#DashBoard .page-link:focus {
  box-shadow: 0 0 0 0.2rem transparent;
}

#DashBoard .page-item.disabled .page-link {
  color: #93959c;
}

#DashBoard .table .ServiceProfile {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
}

#DashBoard .table .ServiceProfile img {
  width: 100%;
  object-fit: contain;
}

#DashBoard .table th,
#DashBoard .table td {
  border-top: 1px solid #33363d;
  padding: 1rem;
}

#DashBoard .table thead th {
  border: 0px;
  border-bottom: 0px;
  color: #d0d2d9;
  font-weight: normal;
}

/*  #DashBoard .row {
        height: 100%;
    }*/

.table.b-table > thead > tr > [aria-sort="descending"] {
  background-image: url("../assets/arrow_down.png") !important;
}

.table.b-table > thead > tr > [aria-sort="ascending"],
.table.b-table > tfoot > tr > [aria-sort="ascending"] {
  background-image: url("../assets/arrow_up.png") !important;
}

.table.b-table > thead > tr > [aria-sort],
.table.b-table > tfoot > tr > [aria-sort] {
  background-size: 7px 5px !important;
}

.table.b-table > thead > tr > [aria-sort="none"],
.table.b-table > tfoot > tr > [aria-sort="none"] {
  background-image: none !important;
}

#DashBoard .input-group {
  width: 25%;
}

#DashBoard .filterInput {
  position: relative;
  background: transparent;
  border: 1px solid #505359;
  border-radius: 15px;
  padding-left: 40px;
  width: 30%;
}

#DashBoard .input-group.input-group-sm:before {
  content: "";
  width: 18px;
  height: 18px;
  /* background: black; */
  left: 12px;
  position: absolute;
  top: 49%;
  transform: translateY(-50%);
  background-image: url("../assets/Search.png");
}

.CallInHistoryWrap::-webkit-scrollbar {
  width: 4px;
  background-color: #fff;
  cursor: pointer;
}

.CallInHistoryWrap::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px white;
  background-color: #fff;
}

.CallInHistoryWrap:hover::-webkit-scrollbar {
  width: 4px;
  background-color: #dbdbdb;
  cursor: pointer;
}

.CallInHistoryWrap:hover::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #cdcbcb;
}

.moreBtn {
  cursor: pointer;
}

.mobile-show {
  display: none !important;
}

.rowDetail {
  color: #d0d2d9;
  background: #23262b;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.3);
  border-radius: 0px 0px 10px 10px;
  padding: 20px;
  font-size: 14px;
}

.rowDetail hr {
  background: #33363d;
}

.rowDetail .rowDetailTitle {
  color: #d0d2d9;
  padding-right: 20px;
}

.rowDetail .rowDetailContnet {
  color: #fcfcfc;
  font-size: 16px;
}

/*--------------Datable樣式End--------------------*/

#DashBoard .tab-item {
  padding: 5px 12px;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 10px;
  display: flex;
  align-items: center;
}

#DashBoard .tab-item.active {
  background: #0090ff;
}

.vdatetime-input {
  width: 0px;
}

#DashBoard .datePicker {
  width: 215px;
  background: transparent;
  border-radius: 15px;
  padding: 5px 15px;
  border: 1px solid #505359;
  color: #d0d2d9;
  cursor: pointer;
}

#DashBoard .datePicker input {
  background: transparent;
  border: 0;
  color: white;
  display: flex;
  height: 100%;
  cursor: pointer;
}

.rowHover {
  transition: 0.3s;
}

.rowHover:hover {
  background: #292b35;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  #DashBoard .DataCategoryWrap.mb-0 {
    margin-bottom: 30px !important;
  }

  #DashBoard .DataCategory {
    height: auto;
  }

  #DashBoard .BusinessBox {
    flex-wrap: wrap;
