<template>
    <div class="style-background-gray " id="ChartLine" style="">
        <div class="d-flex justify-content-between align-items-start Panel-Style">
            <div class="Penaltitle gray-text" @click="setSize()">
                轉預約
            </div>
            <div class="d-flex align-items-end">
                <div class="mr-3" style="padding-bottom: 7px;">
                    <div class="line-wrap">
                        <span class="lineCate blue"></span> LASIK
                    </div>
                    <div class="line-wrap">
                         <span class="lineCate orange"></span> CATA
                    </div>
                </div>
                <div>
                    <span class="xl-text font-weight-bold">38</span>次
                </div>
            </div>
        </div>
        <div class="LasikTrendChart">
            <TrendChart :datasets="TrendChartData.datasets"
                        :grid="TrendChartData.grid"
                        :labels="TrendChartData.labels"
                        :min="0"
                        :interactive="TrendChartData.interactive"
                        :options="TrendChartData.options"
                        @mouse-move="onMouseMove">
            </TrendChart>
            <div id="tooltip" role="tooltip" ref="tooltip" :class="{'is-active': tooltipData}">
                <div class="tooltip-container" v-if="tooltipData" >
                    <strong>{{TrendChartData.labels.xLabels[tooltipData.index]}}</strong>
                    <div class="tooltip-data">
                        <div class="tooltip-data-item tooltip-data-item--1"><div class="square blue"></div>{{tooltipData.data[0]}}</div>
                        <div class="tooltip-data-item tooltip-data-item--1"><div class="square orange"></div>{{tooltipData.data[1]}}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import Vue from "vue";
    import TrendChart from "vue-trend-chart";
    import { createPopper } from '@popperjs/core';

    Vue.use(TrendChart,createPopper);
    export default {
        data() {
            return {
                currentInfo: null,
                tooltipData: null,
                popper: null,
                popperIsActive: false,
                TrendChartData: {
                    datasets : [
                        {
                            data: [10, 50, 20, 100, 40, 60, 80],
                            smooth: true,
                            fill: true,
                            showPoints: true,
                            className: "curve1"
                        },
                         {
                          data: [15, 30, 35, 100, 35, 27, 15],
                          smooth: true,
                          showPoints: true,
                          className: "curve2"
                        },
                    ],
                    interactive:true,
                    grid : {
                        verticalLines: true,
                        horizontalLines: true
                    },
                    labels : {
                        xLabels: ["星期一", "星期二", "星期三", "星期四", "星期五", "星期六", "星期日"],
                        yLabels: 5
                    },
                    options: {
                      responsive: true,
                      maintainAspectRatio: false
                    }
                }
            }
        },
        methods: {
            setSize() {
                const chart = document.getElementById("ChartLine");
                const newWidth = chart.clientWidth;
                this.width = newWidth + 50 + 'px';
               
            },
            ChangeCategroy(index) {
                this.num = index
            },
            initPopper() {
                const chart = document.querySelector(".LasikTrendChart");
                const ref = chart.querySelector(".active-line");
                const tooltip = this.$refs.tooltip;
                this.popper = createPopper(ref, tooltip, {
                    placement: 'right',
                    modifiers: {
                        name: 'arrow',
                        options: {
                            padding: 5, // 5px from the edges of the popper
                        },
                    }
                })
            },
            numberWithSpaces(n) {
              return n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ");
            },
            onMouseMove(params) {
                this.popperIsActive = !!params;
                this.popper.update();
                this.tooltipData = params || null;
            }
        },
        mounted() {
            this.initPopper();
        },

    }
</script>

<style>
    .square{
        width:10px;
        height:10px;
        display:inline-block;
        margin-right:10px;
    }
        .square.orange {
            background: #FF955C;
        }
        .square.blue {
            background: #0090FF;
        }

    #ChartLine {
        padding-bottom: 0;
        width: 100%;
        height: 230px;
        border-radius: 25px;
        overflow: hidden;
    }

    .LasikTrendChart .vtc {
        position: relative;
        height: 150px;
        width: calc(100% + 57px);
        left: -30px;
    }


    .LasikTrendChart .curve.curve1 .fill {
        fill: #0090FF !important;
        opacity: 0.1;
    }
    .LasikTrendChart .curve.curve1 .stroke {
        stroke: #0090FF !important;
    }

    .LasikTrendChart .curve.curve2 .fill {
        fill: #FF955C !important;
        opacity: 0.1;
    }

    .LasikTrendChart .curve.curve2 .stroke {
        stroke: #FF955C !important;
    }

    .LasikTrendChart .curve .point {
        fill: transparent;
        stroke: transparent;
        transition: .3s;
    }

    .LasikTrendChart .curve.curve1 .point.is-active {
        fill: #0090FF;
        stroke: #0090FF;
        stroke-width: 5;
    }

        .LasikTrendChart .curve.curve2 .point.is-active {
            fill: #FF955C;
            stroke: #FF955C;
            stroke-width: 5;
        }

    .LasikTrendChart .labels {
        display: none;
    }

    .LasikTrendChart .line {
        display: none;
    }

    .LasikTrendChart .active-line {
        stroke: #46464696;
    }

    #tooltip.is-active {
        color: white;
        padding: 5px 10px;
        border-radius: 4px;
        font-size: 13px;
        background: #23262B;
        border: 1px solid #33363D;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.3);
        border-radius: 15px;
        width: 80px;
        margin-bottom: 20px;
    }

    .line-wrap{
        display:flex;
        align-items:center;
    }

    .lineCate {
        display: inline-block;
        width: 50px;
        height: 2px;
        margin-right:10px;
    }

        .lineCate.blue {
            background: #0090FF;
        }

        .lineCate.orange {
            background: #FF955C;
        }


        @media screen and (max-width:992px) {
            #ChartLine .style-background-gray {
            padding: 0px;
        }

        #ChartLine {
            padding:0px !important;
        }
    }
</style>