<template>
    <div class="style-background-gray Panel-Style" style="margin-bottom:35px;">
        <b-overlay :show="status" rounded="sm" variant="dark" opacity="0.9">
            <div >
                <div class="Penaltitle gray-text">
                    平均效率分析
                </div>
                <div class="d-flex justify-content-between align-items-center PhoneTimeData">
                    <div class="d-flex flex-column">
                        <p class="mb-5"><span class="gray-text mr-2" >接聽需時</span><span class="xl-text font-weight-bold">9s</span></p>
                        <p class="mb-0"><span class="gray-text mr-2" >平均通話</span><span class="xl-text font-weight-bold">3m12s</span></p>
                    </div>
                </div>
            </div>
        </b-overlay>
    </div>
</template>
<script>
import { mapState } from "vuex"

    export default {
        props:['status'],
        data() {
            return {
               MyPhoneTimeData:''
            }
        },
         watch: {
             'CTI.PhoneTimeData': function (newQuestion, oldQuestion) {
                this.MyPhoneTimeData = newQuestion
            }
        },
        methods: {
           
        },
        computed: {
            ...mapState(['CTI']),
            PhoneTimeMs: function () {
                    let second = parseInt(this.MyPhoneTimeData[0].phoneTime) / parseInt(this.MyPhoneTimeData[0].count)

                    second = parseInt(second)

                    let hours = Math.floor(second / 3600)

                    second = second - hours * 3600

                    let mimute = Math.floor(second / 60)

                    second = second - mimute * 60

                    return ('0' + mimute).slice(-1) + 'm' + ('0' + second).slice(-2) + 's'
                 
            },
            WaitTimeMs: function () {
                let second = parseInt(this.MyPhoneTimeData[0].watiPhoneTime) / parseInt(this.MyPhoneTimeData[0].count)
                second = parseInt(second)
                 return second
                 
            }
        }
    }
</script>
<style>

    #DeskPhoneInfo .TodayPhoneCate .tab-item {
        padding: 5px 12px;
        cursor: pointer;
        transition: .3s;
        border-radius: 10px;
    }

        #DeskPhoneInfo .TodayPhoneCate .tab-item.active {
            background: #0090FF;
        }

    .PhoneTimeData{
        margin-top:30px;
    }

   
</style>